/**
 * 工会模块
 */
import request from "@/config/request";

const association = {
  namespaced: true,
  state: {
    // 当前工会信息
    associationInfoCurrent: {},
  },
  mutations: {
    setInfoCurrent(state, associationInfoCurrent) {
      state.associationInfoCurrent = associationInfoCurrent;
    },
  },
  actions: {
    // 获取当前工会信息
    async getAssociationInfoCurrent({ commit }) {
      const res = await request("association.infoCurrent");
      console.log("res :>> ", res);
      commit("setInfoCurrent", res);
    },
  },
};
export default association;
